/* Global styling */
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;600&display=swap');

body, html {
  font-family: 'Mulish', sans-serif;
}

/* Navbar styling för större skärmar */
.header-container {
  width: 100%;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
  border: none;
  box-shadow: none;
}

.navbar-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 50px;
  background-color: white;
  border-bottom: 1px solid #ddd;
  position: relative;
  border: none;
  box-shadow: none;
}

.logo h1 {
  margin: 0;
  font-size: 26px;
  font-weight: bold;
  color: #4a4a4a;
  cursor: pointer;
}

.logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
}

.contact-info {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
}

.contact-info span {
  margin-right: 20px;
  font-size: 12px;
  color: #4a4a4a;
}

.book-btn {
  padding: 5px 25px;
  margin-right: 70px;
  font-size: 14px;
  background-color: #eee;
  border: 1px solid #ddd;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.book-btn:hover {
  background-color: #f0f0f0;
}

.navbar-bottom {
  background-color: #f5f5f5;
  padding: 10px 0;
  border: none; /* Tar bort alla kanter */
  box-shadow: none; /* Tar bort alla skuggor */
  
}

.nav-links {
  list-style-type: none;
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.nav-links li {
  margin: 0 30px;
}

.nav-links li a {
  text-decoration: none;
  font-size: 14px;
  color: #444444;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-links li a:hover {
  color: #333;
  text-decoration: underline;
}

/* Hamburgermeny-styling */
.menu-icon {
  display: none;
  cursor: pointer;
  left: 20px;
  position: relative;
}

.hamburger-icon {
  width: 30px;
  height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hamburger-icon span {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #333;
}

.close-icon {
  font-size: 30px;
  color: #333;
  margin-right: 15px;
}

/* Sidomeny för mobila enheter */
.mobile-menu {
  position: fixed;
  top: 0;
  left: -100%; /* Startar från vänster */
  width: 100%; /* Täck hela skärmen */
  height: 100%;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  transition: left 0.3s ease;
  z-index: 999; /* Prioritera menyn över andra element */
}

.mobile-menu.open {
  left: 0; /* Flytta till vänster 0 för att visa menyn */
}

.mobile-menu-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 15px 20px;
  border-bottom: 1px solid #ddd;
  background-color: #f0f0f0;

}

.menu-title {
  font-size: 16px;
  font-weight: 600;
  color: #444;
}

.mobile-menu-links {
  list-style: none;
  padding: 20px;
}

.mobile-menu-links li {
  margin: 50px 0;
  font-size: 16px;
  margin-top: 10px;
}

.mobile-menu-links li a {
  text-decoration: none;
  color: #444;
  font-weight: 600;
}

/* Mobilanpassning */
@media (max-width: 768px) {
  /* Dölj vanliga navbaren på små skärmar */
  .navbar-bottom, .contact-info {
    display: none;
  }

  /* Visa hamburgermeny på små skärmar */
  .menu-icon {
    display: block;
  }

  /* Centrera loggan */
  .logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .navbar-top {
    justify-content: flex-start;
    padding: 18px 20px;
  }
}
